import React, {useState, useEffect} from 'react'
import { getCategory } from '../services/catalogService'
import CatalogComponent from './CatalogComponent'
import { Container, Row, Col } from 'reactstrap'



const Home = () => {
    
    const [categories, setCategories] = useState([])
    const [category, setCategory] = useState("")
    
    useEffect(()=>{
        console.log("Component Mount")
        getCategory()
        .then(data=>{
            setCategories(data)
            if(Array.isArray(data))
                setCategory(data[0])
        })
    }, [])

    const handleCategoryChange = (event) => {
        console.log("category change ", event.target.innerHTML)
        setCategory(event.target.innerHTML)
    }


    return ( 
        <div className="home">
            {Array.isArray(categories) && categories.length>0 &&
                categories.map(categoryName=>
                    <button className={category===categoryName?"selectedButton":"buttonCustom"} value={categoryName} onClick={handleCategoryChange}>
                        <h4 className="textPadding">{categoryName}</h4>
                    </button>
                )
            }  
            <CatalogComponent category={category}/>
        </div>
     );
}
 
export default Home;