const baseUrl = "https://furnituresstore.herokuapp.com/"

export const getCategory = async () => {
    let res = await fetch(baseUrl + "category")
    res = res.json()
    return res
}

export const getCatalog = async (category) => {
    let res = await fetch(baseUrl + "catalog?category=" + category)
    res = res.json()
    return res
}