import React from 'react'
import {
  Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle, Button
} from 'reactstrap';

const ProductCard = (props) => {

    return ( 
      <Card style={{maxWidth:"300px", minWidth:"250px"}} className="m-1 cardCustom p-3">
        <img src={props.data.img} width="100%" alt="Card image cap" />
        <CardBody>
          <CardTitle tag="h5">{props.data.name.toUpperCase()}</CardTitle>
          <CardSubtitle tag="h6" className="mb-2 text-muted">
            {props.data.rating}
            &nbsp;
            <img src="https://iconsplace.com/wp-content/uploads/_icons/ffe500/256/png/rating-star-icon-19-256.png" width="5%"/>  
          </CardSubtitle>
          <CardText>{props.data.price}</CardText>
          <div className="buttonCustom"><h6 >Order</h6></div>
        </CardBody>
      </Card>
     );
}
 
export default ProductCard;